  methods: {
    dates() {
      let dates = [];
      for (let i = 0; i < 100; i++) {
        let x = Math.floor(Math.random() * (100)) + 100;
        dates.push([i, x])
      }
      return dates;
    },  
    initialize() {
      this.fetchScrapData();
