    fetchScrapData() {
      //let sdate = Date.now.
      let token = localStorage.getItem("token");

      //if (this.fdate === "") {
      //  this.fdate = moment()
      //    .subtract(1, "days")
      //    .format("YYYYMMDD");
      //}


      //let parafdate = moment(this.fdate.toString())
        //.subtract(2, "days")
        //.format("YYYYMMDD");

      //let paratdate = moment(this.tdate.toString())
        //.subtract(1, "days")
