      //if (this.paraStatus === "IN") {
      //  this.paraStatus = "1";
      //} else if (this.paraStatus === "OUT") {
      //  this.paraStatus = "2";
     // }

      let config = {
        headers: {
          token: token,
          "Access-Control-Allow-Origin": "*",
          'Content-Type': 'application/json',
        },
        params: {
          formName: "QAA1030",
          method: "getQAA1031W"
        },
      };


      const data = {
        YEAR: "2021",
        FAC: "2"
      };


      axios
        //.get("/ktgProject/selectQAC3065ODScrapList.mdo", config)
        //.get("/kgjProject/kvm/CommonActionVM.mdo", config)
         .post("/kgjProject/kvm/CommonActionVM.mdo",data, config)
        .then((response) => {

          //this.dsQAA1031List = response.data;

          let dspItmeInfo = {
            flag: false,            
            subTitle: 0,
            Title: "",
          };

          let itemInfo = {
            wyymm: "",
            month: "",
            wrkday: 0,
            sub_wrkday: "",
            reqty: 0, 
            rewt: 0,
            bdqty: 0,
            bdavg: 0.0,
            bdrate: 0.0,
            bdcode: "",
            bddsc: ""
        };

          console.log(response.data);
          this.dsQAA1031List = response.data.RESULT;
          console.log(this.dsQAA1031List);
          let bdqtysum = 0.0;
          this.dsQAA1031List.forEach((item) => {
            itemInfo.wyymm = item.WYYYY;
            itemInfo.month = "All",
            itemInfo.wrkday = item.Y_WRKDAY;
            itemInfo.reqty = item.Y_REQTY;
            itemInfo.rewt = item.Y_WT;
            itemInfo.bdqty += Math.round(parseFloat(item.Y_BDCNT));            
          })

          itemInfo.bdavg = parseFloat(itemInfo.bdqty/itemInfo.wrkday).toFixed(2);
          itemInfo.bdrate = parseFloat(itemInfo.bdqty/itemInfo.reqty * 100).toFixed(2);

          this.dsItemTotal.splice(0,this.dsItemTotal.length);
         
          this.dsItemTotal.push(itemInfo);

          
          //this.dsItemTotal.push(dspItmeInfo);

          console.log(itemInfo);
          console.log(this.dsItemTotal);
          
        })
        .catch((error) => {
          console.log(error);
        });
    },

